@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    /* background-image: linear-gradient(to bottom, #47e26e 50%, #2e380c 100%); */
    background-color: antiquewhite;
    font-family: 'Montserrat', sans-serif;
}
.app-container {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 65px;
}

.text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px 0;
}
.background_shade {
    position: absolute;
    top: -65px;
    left: 0;
    max-height: 65px;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.829);
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: rgba(228, 228, 228, 0.774);
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 700;
}
.form-frame {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: 2px 3px 9px -1px black;
    background-color: rgba(43, 27, 10, 0.788);
    height: 150px;
    width: 400px;
    margin-bottom: 30px;
    overflow-y: auto;
    overflow-x: hidden;
    border-radius: 5px;
    border: 2px solid rgb(77, 77, 77);
}

.form-elements {
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
}

.form-item {
    padding-right: 10px;
    /* height: ; */
}

.form-item input{
    height: 30px;
    border-radius: 5px;
}

.form-calorie input{
    height: 30px;
    border-radius: 5px;
}

.form-frame button {
    background-color: burlywood;
    border-radius: 5px;
    width: 85%;
}
.card {
    width: 90%;
    border: 1px solid rgb(49, 16, 16);
    background: burlywood;
    padding: 5px;
    margin: 6px;
    border-radius: 5px;
    box-shadow: 3px 1px 9px -1px #1a061b;
    height: 90px;
    padding: 15px;
    color: rgb(15, 12, 12);
  }

  .card-temp {
      background:rgb(15, 12, 12);
  }

  .btn-del {
      background: rgb(202, 22, 22);
      color: whitesmoke;
      border-radius: 4px;
      height: 23px;
      margin-right: 1em;
  }

  .btn-edit {
      background: rgb(61, 61, 212);
      color: whitesmoke;
      border-radius: 4px;
      height: 23px;        
  }
